
.weather_days-list{
    max-width: 85%;
    margin: 20px auto 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.days_list-item{

    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 15px 0;
    width: 20%;
   
    background-color: #1D282A;
    text-align: center;
    cursor: pointer;

    transition: border 0.1s ease;
    transition: transform 1.2s;
    border-radius: 10px;
    border-left: 1px solid #ccc;
}


.days_list-item:first-child{
    border:none;
}

.days_list-item:hover + .days_list-item{
    border: none;
}

.days_list-item:hover{
    border: 2px solid #2395FF;
    transform: translateY(-10px);
}

.list_item-date{
    font-size: 13px;
    
}

.list_item-weather{
    font-size: 17px;
    line-height: 1.2;
    
}