.main_weather_info{
    background-color: #1D282A;
    width: 30%;
    margin-right: -1px;
}

.extra_weather_info{
    background-color: #1D282A;
    width: 40%;
    height: 50%;
    margin: 33px 0 0 -1px ;
}

.temperature{
    color: #2395FF;
    font-size: 52px;
    font-weight: 300;
    line-height: 1.1;
}

.list_characteristics{
    padding: 20px;
}
.list_characteristic-item{
    display: grid;
    grid-template-columns: 10% 30% 25% 30%;
    grid-gap: 7px;
    grid-auto-rows: 50px;
    align-items: center;
    justify-content: start;
    font-size: 13px;  
    text-align: center;
    
}

.characteristic_value{
    font-size: 14px;
}
.characteristic_title{
    color: #989696;
}

.date{
    padding: 15px;
    color: #989696;
    font-size: 15px;
    width: 190px;
    text-align: center;
    line-height: 1.8;
}

.weather_info-box{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: -20px 20px 0 0;
}

.weather_temperature-box span{
    display: block;
    color: #989696;
}

.city span{
    margin-left: 10px;
}

.city{
    margin-top: 20px;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center; 
}
