*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: #fff;
    border: none;
    list-style: none;
}

button{
    background:none;
    cursor: pointer;
}

body{
    background-color: #000;
    font-family: McLaren;
}

.logo{
    position: absolute;
}

.main_app{
    position: absolute;
    background: url(../../img/background.png) no-repeat;
    background-size: cover;
    width: 68%;
    height: 95%;
    margin: 20px 240px;
}

.search_bar{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0;
}

main{
    display: flex;
    align-content: center;
    justify-content: center;
    margin-top: 30px;
   
}

.buttons{
    display: flex;
    margin: 70px 0 20px 100px;
}

.buttons button:not(:last-child)
{
    margin-right: 30px;
}

